@import url(https://fonts.googleapis.com/css?family=Arimo);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
p, h2{
  color: #8e2807;
  
}

.result{
  display: flex;
  justify-content: space-around;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
 body {
	 display: flex;
	 justify-content: center;
}
 .wrapperCalculator {
	 margin-top: 5%;
	 display: flex;
	 justify-content: center;
	 font-family: "Arimo";
	 background-color: #B5C9C3;
	 box-shadow: 9px 13px 25px 0px rgba(0, 0, 0, 0.18);
	 animation: slideUp 2000ms ease;
}

.match{
  display: flex;
  flex-direction: row;
  overflow-x: scroll;
  justify-content: center;
}
 @keyframes slideUp {
	 0% {
		 transform: translateY(100%);
		 visibility: visible;
	}
	 100% {
		 transform: translateY(0);
	}
}
 .container {
	 width: 65%;
	 padding: 5% 10%;
}
 h1 {
	 align-self: center;
}
 form {
	 width: 100%;
}
 form > * {
	 margin-top: 20px;
}
 form input {
	 width: 200px;
	 max-height: 25px;
	 border: 0;
	 font-size: 1rem;
	 letter-spacing: 0.15rem;
	 font-family: "Arimo";
	 margin-bottom: 5px;
	 color: #8e2807;
	 border-radius: 4px;
}
 form label {
	 text-transform: uppercase;
	 font-size: 11px;
	 letter-spacing: 2px;
   color: #8e2807;
   margin-right: 20px;
}
 form h1 {
	 font-size: 24px;
	 line-height: 10px;
	 color: #493b76;
	 letter-spacing: 1px;
}
 form h1:nth-of-type(2) {
	 margin-top: 10%;
}
 .name {
	 justify-content: space-between;
	 display: flex;
	 width: 100%;
}

 .name label {
	 margin: 0 20px;
}
 .address-info {
	 display: flex;
	 justify-content: space-between;
}

 .cc-info {
	 display: flex;
	 justify-content: space-between;
}
 .cc-info div {
	 width: 45%;
}

select {
  position: relative;
  width: 100px;
  margin: 0 20px;
  font-family: 'Open Sans', 'Helvetica Neue', 'Segoe UI', 'Calibri', 'Arial', sans-serif;
  font-size: 18px;
  color: #60666d;
}

option {
  display: block;
  padding: 15px;
  background-color: #fff;
}
option:hover, option:focus {
  color: #546c84;
  background-color: #fbfbfb;
}
#roomsNumber {
  display: flex;
  flex-direction: row;
  justify-content: center;
  max-height: 30px;
}
 .btns {
	 display: flex;
	 flex-direction: column;
	 align-items: flex-end;
}
 .btns  {
	 margin: 3px 0;
	 height: 30px;
	 color: #cfc9e1;
	 background-color: #4a3b76;
	 text-transform: uppercase;
	 border: 0;
	 border-radius: 0.3rem;
   letter-spacing: 2px;
   margin: 50px;
   display: inline
}
 .btns:hover {
	 animation-name: btn-hov;
	 animation-duration: 550ms;
	 animation-fill-mode: forwards;
}
 @keyframes btn-hov {
	 100% {
		 background-color: #cfc9e1;
		 color: #4a3b76;
		 transform: scale(1.05);
	}
}
 input:focus, button:focus {
	 outline: none;
}
 @media (max-width: 736px) {
	 .wrapperCalculator {
		 width: 100%;
	}
	 .container {
		 width: 100%;
	}
	 .btns {
		 align-items: center;
	}
	 .btns button {
		 width: 50%;
  }
  .roomsNumber {
    display: flex;
    flex-direction: row;
  }
	 form h1 {
		 text-align: center;
	}
	 .name, .address-info, .cc-info {
		 flex-direction: column;
		 width: 100%;
     justify-content: space-between;
     align-items: center;
	}
	 .name div, .address-info div, .cc-info div {
		 align-items: center;
		 flex-direction: column;
		 width: 100%;
		 display: flex;
	}
	 .street, .cc-num {
		 text-align: center;
	}
	 input {
		 margin: 5px 0;
		 max-height: 30px;
	}
}
 
/* LOGIN */

.main-content{
	width: 50%;
	border-radius: 20px;
	box-shadow: 0 5px 5px rgba(0,0,0,.4);
	margin: 5em auto;
	display: flex;
}
.company__info{
	background-color: #008080;
	border-top-left-radius: 20px;
	border-bottom-left-radius: 20px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	color: #fff;
}
.fa-android{
	font-size:3em;
}
.check {
    display: flex;
    flex-direction: row;
}
@media screen and (max-width: 640px) {
	.main-content{width: 90%;}
	.company__info{
		display: none;
	}
	.login_form{
		border-top-left-radius:20px;
		border-bottom-left-radius:20px;
	}
}
