	.main-content{width: 70%;}
}
.row > h2{
  color:#008080;
  margin-bottom: 15px;
  margin-top: 15px;
}
  .row {
  justify-content: space-around;
}  
.login_form{
	background-color: #fff;
	border-top-right-radius:20px;
	border-bottom-right-radius:20px;
	border-top:1px solid #ccc;
	border-right:1px solid #ccc;
}
form{
	padding: 0 2em;
}
.form__input{
	width: 100%;
	border:0px solid transparent;
	border-radius: 0;
	border-bottom: 1px solid #aaa;
	padding: 1em .5em .5em;
	padding-left: 2em;
	outline:none;
	margin:1.5em auto;
	transition: all .5s ease;
}
.form__input:focus{
	border-bottom-color: #008080;
	box-shadow: 0 0 5px rgba(0,80,80,.4); 
	border-radius: 4px;
}
.btnB{
	transition: all .5s ease;
	width: 70%;
	border-radius: 30px;
	color:#008080;
	font-weight: 600;
	background-color: #fff;
	border: 1px solid #008080;
	margin-top: 1.5em;
  margin-bottom: 1em;
  padding: 5px;
}
.btnB:hover, .btnB:focus{
	background-color: #008080;
	color:#fff;
}

.btnSelect {
	transition: all .5s ease;
    width: 70%;
    color: #008080;
    font-weight: 600;
    background-color: #fff;
    border: 1px solid #008080;
    margin-top: 1.5em;
    margin-bottom: 1em;
	padding: 5px;
	margin-bottom: -10px;
